<template>
  <div class="container">
    <shop-header @cast-item="getItem" />
    <shop-content :shopName="title" />
    <shop-cart :shopName="title" />
  </div>
</template>

<script>
import ShopCart from "./ShopCart.vue";
import ShopContent from "./ShopContent.vue";
import ShopHeader from "./ShopHeader.vue";
export default {
  name: "Shop",
  components: { ShopHeader, ShopContent, ShopCart },

  data() {
    return {
      title: "",
    };
  },

  methods: {
    getItem(val) {
      this.title = val;
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  padding: 0 18rem;
}
</style>
