<template>
  <div
    class="mask"
    v-if="cartShow && JSON.stringify(cartItem) !== '{}'"
    @click="
      () => {
        handleCartShowChange(cartItem);
      }
    "
  ></div>
  <div class="cart">
    <div class="cart__detail">
      <div
        class="cart__detail__title"
        v-if="cartShow && JSON.stringify(cartItem) !== '{}'"
      >
        <span
          class="cart__detail__title__checked iconfont"
          v-if="allChecked"
          @click="
            () => {
              setAllChecked(shopId);
            }
          "
        >
          &#xe844;
        </span>
        <span
          class="cart__detail__title__checked iconfont"
          v-else
          @click="
            () => {
              setAllChecked(shopId);
            }
          "
        >
          &#xe933;
        </span>
        <span class="cart__detail__title__left">全选</span>
        <span
          class="cart__detail__title__right"
          @click="
            () => {
              clearCart(shopId);
            }
          "
          >清空购物车</span
        >
      </div>

      <div
        class="cart__detail__list"
        v-if="cartShow && JSON.stringify(cartItem) !== '{}'"
      >
        <div
          class="cart__detail__list__item"
          v-for="item in cartItem"
          :key="item.id"
        >
          <span
            @click="
              () => {
                changeItemChecked(shopId, item.id);
              }
            "
            v-if="item.isChecked"
            class="cart__detail__list__item__checked iconfont"
          >
            &#xe844;
          </span>
          <span
            @click="
              () => {
                changeItemChecked(shopId, item.id);
              }
            "
            v-else
            class="cart__detail__list__item__checked iconfont"
          >
            &#xe933;
          </span>
          <img
            class="cart__detail__list__item__img"
            :src="item.imgUrl"
            alt=""
          />
          <div class="cart__detail__list__item__text">
            <div class="cart__detail__list__item__text__title">
              {{ item.name }}
            </div>
            <div class="cart__detail__list__item__text__pirce">
              <span
                class="cart__detail__list__item__text__pirce__current--pirce"
              >
                ￥{{ item.promotionPrice }}
              </span>
              <span
                class="cart__detail__list__item__text__pirce__original--pirce"
              >
                ￥{{ item.originalPrice }}
              </span>
            </div>
            <div class="cart__detail__list__item__text__num">
              <span
                @click="
                  () => {
                    changeItemNumToCart(shopId, shopName, item.id, item, -1);
                  }
                "
                v-show="cartData?.[shopId]?.itemList?.[item.id]?.['count']"
                class="cart__detail__list__item__text__num__minus iconfont"
              >
                &#xe619;
              </span>
              <span
                v-show="cartData?.[shopId]?.itemList?.[item.id]?.['count']"
                class="cart__detail__list__item__text__num__number"
              >
                {{ cartData?.[shopId]?.itemList?.[item.id]?.["count"] }}
              </span>
              <span
                @click="
                  () => {
                    changeItemNumToCart(shopId, shopName, item.id, item, 1);
                  }
                "
                class="cart__detail__list__item__text__num__plus iconfont"
              >
                &#xe845;
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="cart__content">
      <div class="cart__content__left">
        <img
          @click="
            () => {
              handleCartShowChange(cartItem);
            }
          "
          src="https://markdown-1253389072.cos.ap-nanjing.myqcloud.com/202202261637089.png"
          alt=""
          class="cart__content__left__img"
        />
        <div class="cart__content__left__num">{{ total }}</div>
        <div class="cart__content__left__count">总计：</div>
        <div class="cart__content__left__price">￥{{ sumPrice }}</div>
      </div>
      <router-link
        :to="total ? { path: `/order/orderConfirmation/${shopId}` } : ''"
      >
        <div class="cart__content__right">去结算</div>
      </router-link>
    </div>
  </div>
</template>
<script>
import { computed, ref } from "vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import { useCommonCartEffect } from "../../effects/CommonCartEffect";

const useCartEffect = (cartShow) => {
  const store = useStore();
  const route = useRoute();

  const { cartData } = store.state;

  const shopId = route.params.id;

  const total = computed(() => {
    const itemList = cartData[shopId]?.itemList || {};
    let count = 0;
    if (itemList) {
      for (const key in itemList) {
        if (itemList[key].isChecked) {
          count += itemList[key].count;
        }
      }
    }
    return count;
  });

  const sumPrice = computed(() => {
    const itemList = cartData[shopId]?.itemList || {};
    let sum = 0.0;
    if (itemList) {
      for (const key in itemList) {
        if (itemList[key].isChecked) {
          sum += itemList[key].count * itemList[key].promotionPrice;
        }
      }
    }
    return sum.toFixed(2);
  });

  const cartItem = computed(() => {
    const cartItem = cartData[shopId]?.itemList || {};

    if (JSON.stringify(cartItem) === "{}") {
      cartShow.value = false;
    }

    return cartItem;
  });

  const allChecked = computed(() => {
    const itemList = cartData[shopId]?.itemList || {};
    let result = true;

    for (const key in itemList) {
      if (!itemList[key].isChecked) {
        result = false;
      }
    }

    return result;
  });

  return { total, sumPrice, cartItem, shopId, allChecked };
};

const useCartShowEffect = () => {
  const cartShow = ref(false);
  const handleCartShowChange = (cartItem) => {
    if (JSON.stringify(cartItem) !== "{}") {
      cartShow.value = !cartShow.value;
    }
  };

  return { cartShow, handleCartShowChange };
};

export default {
  name: "ShopCart",
  props: ["shopName"],

  setup() {
    const { cartShow, handleCartShowChange } = useCartShowEffect();
    const { total, sumPrice, cartItem, shopId, allChecked } =
      useCartEffect(cartShow);
    const {
      cartData,
      changeItemNumToCart,
      changeItemChecked,
      clearCart,
      setAllChecked,
    } = useCommonCartEffect();

    return {
      total,
      sumPrice,
      cartItem,
      cartData,
      shopId,
      allChecked,
      cartShow,
      handleCartShowChange,
      changeItemNumToCart,
      changeItemChecked,
      clearCart,
      setAllChecked,
    };
  },
};
</script>
<style lang="scss" scoped>
@import "../../style/mixins.scss";

.mask {
  position: fixed;
  top: 0;
  bottom: 49rem;
  left: 0;
  right: 0;
  background: rgba($color: #000000, $alpha: 0.5);
}

.cart {
  &__detail {
    position: fixed;
    bottom: 49rem;
    left: 0;
    right: 0;
    background: white;
    width: 100%;

    &__title {
      height: 52rem;
      padding: 0 18rem;
      box-sizing: border-box;
      line-height: 52rem;
      font-family: PingFangSC-Regular;
      font-size: 14rem;
      color: #333333;
      border-bottom: 1rem solid #f1f1f1;

      &__checked {
        float: left;
        font-size: 20rem;
        color: #0091ff;
      }

      &__left {
        float: left;
        margin-left: 8rem;
      }

      &__right {
        float: right;
      }
    }

    &__list {
      flex: 1;
      overflow-y: auto;

      &__item {
        height: 80rem;
        margin-left: 16rem;
        margin-right: 18rem;
        display: flex;
        box-sizing: border-box;
        padding: 12rem 0;
        position: relative;

        &__checked {
          font-size: 20rem;
          line-height: 56rem;
          color: #0091ff;
        }

        &__img {
          width: 46rem;
          height: 46rem;
          margin-left: 16rem;
        }

        &__text {
          width: 60%;
          margin-left: 16rem;

          &__title {
            font-family: PingFangSC-Medium;
            font-size: 14rem;
            color: #333333;
            font-weight: 700;
            margin-bottom: 10rem;
            @include ellipsis;
          }

          &__pirce {
            display: inline-block;
            font-family: PingFangSC-Semibold;

            &__current--pirce {
              font-size: 14rem;
              color: #e93b3b;
              font-weight: 700;
            }

            &__original--pirce {
              font-size: 10rem;
              color: #999;
              text-decoration: line-through;
            }
          }

          &__num {
            font-family: PingFangSC-Regular;
            height: 25rem;
            text-align: center;
            position: absolute;
            right: 0;
            bottom: 40rem;
            display: flex;

            &__number {
              width: 25rem;
              font-size: 14rem;
              margin: 5rem 0rem;
              color: #333333;
            }

            &__minus,
            &__plus {
              font-size: 20rem;
              color: #666;
            }

            &__plus {
              color: #0091ff;
            }
          }
        }
      }
    }
  }

  &__content {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 49rem;
    display: flex;
    line-height: 49rem;

    &__left {
      flex: 1;
      height: 100%;
      display: flex;
      align-items: center;
      position: relative;

      &__img {
        width: 28rem;
        height: 26rem;
        margin-left: 24rem;
        margin-right: 32rem;
      }

      &__num {
        min-width: 20rem;
        height: 20rem;
        border-radius: 10rem;
        position: absolute;
        left: 38rem;
        top: 0rem;
        line-height: 20rem;
        text-align: center;
        background: #e93b3b;
        font-family: PingFangSC-Medium;
        font-size: 16rem14rem;
        color: #ffffff;
        transform: scale(0.5, 0.5);
        transform-origin: center center;
      }

      &__count {
        line-height: 20rem;
        font-family: PingFangSC-Regular;
        font-size: 12rem14rem;
        color: #333333;
      }
      &__price {
        font-weight: 700;
        font-family: PingFangSC-Medium;
        font-size: 18rem14rem;
        color: #e93b3b;
      }
    }

    &__right {
      width: 98rem;
      height: 100%;
      background: #4fb0f9;
      font-family: PingFangSC-Medium;
      font-size: 14rem;
      color: #ffffff;
      text-align: center;
    }
  }
}
</style>
