<template>
  <div class="content">
    <div class="content__left">
      <div
        @click="currentItemName = item.name"
        v-for="item in leftItem"
        :key="item.text"
        :class="{
          content__left__item: true,
          content__left__item__active: item.name === currentItemName,
        }"
      >
        {{ item.text }}
      </div>
    </div>

    <div class="content__right">
      <div
        class="content__right__item"
        v-for="item in rightItem"
        :key="item.id"
      >
        <img class="content__right__item__img" :src="item.imgUrl" alt="" />
        <div class="content__right__item__text">
          <div class="content__right__item__text__title">{{ item.name }}</div>
          <div class="content__right__item__text__work--off">
            月售{{ item.sales }}件
          </div>
          <div class="content__right__item__text__pirce">
            <span class="content__right__item__text__pirce__current--pirce">
              ￥{{ item.promotionPrice }}
            </span>
            <span class="content__right__item__text__pirce__original--pirce">
              ￥{{ item.originalPrice }}
            </span>
          </div>
          <div class="content__right__item__text__num">
            <span
              @click="
                () => {
                  changeItemNumToCart(shopId, shopName, item.id, item, -1);
                }
              "
              v-show="cartData?.[shopId]?.itemList?.[item.id]?.['count']"
              class="content__right__item__text__num__minus iconfont"
            >
              &#xe619;
            </span>
            <span
              v-show="cartData?.[shopId]?.itemList?.[item.id]?.['count']"
              class="content__right__item__text__num__number"
            >
              {{ cartData?.[shopId]?.itemList?.[item.id]?.["count"] }}
            </span>
            <span
              @click="
                () => {
                  changeItemNumToCart(shopId, shopName, item.id, item, 1);
                }
              "
              class="content__right__item__text__num__plus iconfont"
            >
              &#xe845;
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, watchEffect } from "vue";
import { useRoute } from "vue-router";
import { get } from "../../utils/request.js";
import { useCommonCartEffect } from "../../effects/CommonCartEffect";

const currentItemName = ref("all");

const useShopLeftItemEffect = () => {
  const route = useRoute();
  const leftItem = ref([]);

  const getItemData = async () => {
    try {
      const result = await get(`/shop/${route.params.id}/leftItem`);
      if (result.data.length == 0) {
        console.log("服务器未响应!!!");
      } else {
        leftItem.value = result.data;
      }
    } catch (error) {
      console.log(error);
    }
  };
  getItemData();

  return { leftItem, currentItemName };
};

const useShopRightItemEffect = () => {
  const route = useRoute();
  const rightItem = ref([]);
  const shopId = route.params.id;

  const getItemData = async (shopId, currentTab) => {
    try {
      const result = await get(`/shop/${shopId}/tab/${currentTab}`);
      if (result.data.code === "0000") {
        rightItem.value = result.data.data;
      } else {
        console.log("服务器未响应!!!");
      }
    } catch (error) {
      console.log(error);
    }
  };

  watchEffect(() => {
    getItemData(shopId, currentItemName.value);
  });

  return { rightItem, shopId };
};

export default {
  name: "ShopContent",
  props:['shopName'],

  setup() {
    const { leftItem, currentItemName } = useShopLeftItemEffect();
    const { rightItem, shopId } = useShopRightItemEffect();
    const { cartData, changeItemNumToCart } = useCommonCartEffect();

    return {
      leftItem,
      rightItem,
      shopId,
      currentItemName,
      cartData,
      changeItemNumToCart,
    };
  },
};
</script>

<style lang="scss" scoped>
@import "../../style/mixins.scss";

.content {
  position: absolute;
  top: 146rem;
  bottom: 49rem;
  left: 0;
  right: 0;
  display: flex;

  &__left {
    width: 76rem;
    height: 100%;
    background: #f5f5f5;
    text-align: center;
    line-height: 40rem;

    &__item {
      width: 100%;
      height: 40rem;
      border-radius: 2rem;
      font-family: PingFangSC-Regular;
      font-size: 14rem;
      color: #333333;

      &__active {
        background: white;
      }
    }
  }

  &__right {
    flex: 1;
    overflow-y: auto;

    &__item {
      height: 92rem;
      margin-left: 16rem;
      margin-right: 18rem;
      border-bottom: 1rem solid #f1f1f1;
      display: flex;
      box-sizing: border-box;
      padding: 12rem 0;
      position: relative;

      &__img {
        width: 68rem;
        height: 68rem;
      }

      &__text {
        width: 60%;
        margin-left: 16rem;

        &__title {
          font-family: PingFangSC-Medium;
          font-size: 14rem;
          color: #333333;
          font-weight: 700;
          @include ellipsis;
        }

        &__work--off {
          font-family: PingFangSC-Regular;
          font-size: 12rem;
          color: #333333;
          margin: 6rem 0;
        }

        &__pirce {
          display: inline-block;
          font-family: PingFangSC-Semibold;

          &__current--pirce {
            font-size: 14rem;
            color: #e93b3b;
            font-weight: 700;
          }

          &__original--pirce {
            font-size: 10rem;
            color: #999;
            text-decoration: line-through;
          }
        }

        &__num {
          font-family: PingFangSC-Regular;
          height: 25rem;
          text-align: center;
          position: absolute;
          right: 0;
          bottom: 14rem;
          display: flex;

          &__number {
            width: 25rem;
            font-size: 14rem;
            margin: 5rem 0rem;
            color: #333333;
          }

          &__minus,
          &__plus {
            font-size: 20rem;
            color: #666;
          }

          &__plus {
            color: #0091ff;
          }
        }
      }
    }
  }
}
</style>
