<template>
  <div class="header">
    <div @click="router.back()" class="header__backBtn iconfont">&#xe697;</div>

    <div class="header__search">
      <span class="header__search__search-iconfont iconfont">&#xe65c;</span>
      <input
        type="text"
        class="header__search__input"
        placeholder="请输入商品名称搜索"
        name=""
        id=""
      />
    </div>
  </div>

  <shop-info :item="item" :showBorder="false" v-if="item.imgUrl" />
</template>

<script>
import { ref } from "vue";
import ShopInfo from "@/components/ShopInfo.vue";
import { useRoute, useRouter } from "vue-router";
import { get } from "../../utils/request";

const useShopInfoEffect = (context) => {
  const item = ref({});
  const route = useRoute();

  const getItemData = async () => {
    try {
      const result = await get("/shop/" + route.params.id);
      if (result.data.code === "0000") {
        item.value = result.data.data;

        context.emit("castItem", result.data.data.title);
      } else {
        console.log("服务器没响应!!!");
      }
    } catch (error) {
      console.log(error);
    }
  };
  getItemData();

  return { item };
};

export default {
  name: "ShopHeader",
  components: { ShopInfo },
  emits: ["castItem"],

  setup(props, context) {
    const router = useRouter();
    const { item } = useShopInfoEffect(context);

    return { router, item };
  },
};
</script>

<style lang="scss" scoped>
.header {
  height: 32rem;
  margin: 16rem 0rem 16rem 0;
  display: flex;
  line-height: 32rem;

  &__backBtn {
    color: #b6b6b6;
    margin-right: 16.2rem;
    font-size: 28rem;
  }

  &__search {
    width: 310rem;
    background: #f5f5f5;
    border-radius: 16rem;

    &__search-iconfont {
      font-size: 16rem;
      color: #b7b7b7;
      margin: 0 12.1rem 0 16rem;
    }

    &__input {
      background: #f5f5f5;
      border-radius: 16rem;
      font-family: PingFangSC-Regular;
      font-size: 14rem;
      color: #333333;
    }
  }
}
</style>
