import { toRefs } from "vue";
import { useStore } from "vuex";

export const useCommonCartEffect = () => {
  const store = useStore();
  const { cartData } = toRefs(store.state);

  const changeItemNumToCart = (shopId, shopName, itemId, itemInfo, num) => {
    store.commit("changeItemNumToCart", {
      shopId,
      shopName,
      itemId,
      itemInfo,
      num,
    });
  };

  const changeItemChecked = (shopId, itemId) => {
    store.commit("changeItemChecked", { shopId, itemId });
  };

  const clearCart = (shopId) => {
    store.commit("clearCart", { shopId });
  };

  const setAllChecked = (shopId) => {
    store.commit("setAllChecked", { shopId });
  };


  return {
    cartData,
    changeItemNumToCart,
    changeItemChecked,
    clearCart,
    setAllChecked,
  };
};
